import React , { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
//Components
import Scream from '../components/Scream';
import Categories from '../components/Categories';
import Countries from '../components/Countries';
import NewsSkeleton from '../components/NewsSkeleton';
import Hidden from '@material-ui/core/Hidden';
// mui stuff
const NewsAPI = require('newsapi');
const newsapi = new NewsAPI('9047eaa819904eb49205d2d53ab54356');
// To query /v2/top-headlines
// All options passed to topHeadlines are optional, but you need to include at least one of them


class category extends Component {
    constructor(props){
        super(props);
        this.state = {
            loading: true,
            news: [],
            error: false,
            theError: '',
            category: this.props.match.params.title,
            country: 'All'
        }
    }
    componentDidMount(){
        var networkDataReceived = false;
        if(this.props.match.params.name){
            //fetch fresh data
            var networkUpdate = newsapi.v2.topHeadlines({
                category: this.props.match.params.title,
                country: this.props.match.params.name
              }).then(response => {
                if(response.status === 'ok'){
                    this.setState({
                        loading: false,
                        news: response.articles
                    })
                }  else{
                    this.setState({
                        error: true,
                        theError: "Sorry! Problem loading new headlines!"
                    })
                }
            })
            .catch((err)=>{
                console.log(err)
                this.setState({
                    error: true,
                    theError: "Offline: Turn on data for latest headlines."
                })
            });
            
        // fetch cached data
        caches.match(`https://newsapi.org/v2/top-headlines?category=${this.props.match.params.title}&country=${this.props.match.params.name}`).then(function(res) {
            if (!res) throw Error("No data");
            return res.json();
        }).then(function(response) {
            // don't overwrite newer network data
            if (!networkDataReceived) {
                this.setState({
                    loading: false,
                    news: response.articles
                })
            }
        }).catch(function() {
            // we didn't get cached data, the network is our last hope:
            return networkUpdate;
        }).catch(err=>{
            console.log(err)
            this.setState({
                error: true,
                theError: "Sorry! Problem loading new headlines!"
            })
        })
        } else {
            networkUpdate = newsapi.v2.topHeadlines({
                category: this.props.match.params.title,
              }).then(response => {
                if(response.status === 'ok'){
                    this.setState({
                        loading: false,
                        news: response.articles
                    })
                }  else{
                    this.setState({
                        error: true,
                        theError: "Sorry! Problem loading new headlines!"
                    })
                }
            })
            .catch((err)=>{
                console.log(err)
                this.setState({
                    error: true,
                    theError: "Offline: Turn on data for latest headlines."
                })
            });
            
        // fetch cached data
        caches.match(`https://newsapi.org/v2/top-headlines?category=${this.props.match.params.title}`).then(function(res) {
            if (!res) throw Error("No data");
            return res.json();
        }).then(function(response) {
            // don't overwrite newer network data
            if (!networkDataReceived) {
                this.setState({
                    loading: false,
                    news: response.articles
                })
            }
        }).catch(function() {
            // we didn't get cached data, the network is our last hope:
            return networkUpdate;
        }).catch(err=>{
            console.log(err)
            this.setState({
                error: true,
                theError: "Sorry! Problem loading new headlines!"
            })
        })
        }
    }
    UNSAFE_componentWillReceiveProps(nextProps){
        var networkDataReceived = false;
        if(nextProps.match.params.name){
            this.setState({
                category: nextProps.match.params.title,
                country: nextProps.match.params.name
            })
            var networkUpdate = newsapi.v2.topHeadlines({
                category: nextProps.match.params.title,
                country: nextProps.match.params.name
              }).then(response => {
                if(response.status === 'ok'){
                    this.setState({
                        loading: false,
                        news: response.articles
                    })
                }  else{
                    this.setState({
                        error: true,
                        theError: "Sorry! Problem loading new headlines!"
                    })
                }
            })
            .catch((err)=>{
                console.log(err)
                this.setState({
                    error: true,
                    theError: "Offline: Turn on data for latest headlines."
                })
            });
            
        // fetch cached data
        caches.match(`https://newsapi.org/v2/top-headlines?category=${nextProps.match.params.title}&country=${nextProps.match.params.name}`).then(function(res) {
            if (!res) throw Error("No data");
            return res.json();
        }).then(function(response) {
            // don't overwrite newer network data
            if (!networkDataReceived) {
                this.setState({
                    loading: false,
                    news: response.articles
                })
            }
        }).catch(function() {
            // we didn't get cached data, the network is our last hope:
            return networkUpdate;
        }).catch(err=>{
            console.log(err)
            this.setState({
                error: true,
                theError: "Sorry! Problem loading new headlines!"
            })
        })
        } else {
            this.setState({
                category: nextProps.match.params.title
            })
            networkUpdate = newsapi.v2.topHeadlines({
                category: nextProps.match.params.title,
              }).then(response => {
                if(response.status === 'ok'){
                    this.setState({
                        loading: false,
                        news: response.articles
                    })
                }  else{
                    this.setState({
                        error: true,
                        theError: "Sorry! Problem loading new headlines!"
                    })
                }
            })
            .catch((err)=>{
                console.log(err)
                this.setState({
                    error: true,
                    theError: "Offline: Turn on data for latest headlines."
                })
            });
            
        // fetch cached data
        caches.match(`https://newsapi.org/v2/top-headlines?category=${nextProps.match.params.title}`).then(function(res) {
            if (!res) throw Error("No data");
            return res.json();
        }).then(function(response) {
            // don't overwrite newer network data
            if (!networkDataReceived) {
                this.setState({
                    loading: false,
                    news: response.articles
                })
            }
        }).catch(function() {
            // we didn't get cached data, the network is our last hope:
            return networkUpdate;
        }).catch(err=>{
            console.log(err)
            this.setState({
                error: true,
                theError: "Sorry! Problem loading new headlines!"
            })
        })
        }
    }
    render (){
        const {loading, news, error, theError } = this.state;
        let fetchError = error?(<Typography  variant="body2" color="error">{theError}</Typography>):null;
        let recentScreamMarkUp = !loading?news.map((scream,ind)=><Scream key={`${scream.title}${ind}`} scream={scream}/>):(<NewsSkeleton />)
        return (
            <Grid container spacing={2}>
            {fetchError}
                <Grid item md={10} sm={9} xs={12}>
                    {recentScreamMarkUp}
                </Grid>
                <Hidden xsDown>
                    <Grid item md={2} sm={3} xs={12}>
                        <Categories category={this.state.category} country={this.state.country} urlTo='/worldnews-client/category' />
                        <Countries  category={this.state.category} country={this.state.country} urlTo={`/worldnews-client/category/${this.state.category}`}  />
                    </Grid>
                </Hidden>
            </Grid>
        )
    }
}

export default category;